<template>
  <!-- 权限分配 -->
  <el-container class="permission-assignment">
    <el-aside width='22%'>
      <el-scrollbar style='height: 100%;'>
        <org-tree ref="tree" :treeData="treeData" @node-click="orgTreeClick"></org-tree>
      </el-scrollbar>
    </el-aside>
    <el-main style="padding-top: 0;">
      <el-table
        :data="dataList"
        border
        v-loading="dataListLoading"
        :height="siteContentViewHeight - 50+'px'"
        style="width: 100%;">
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column prop="userCode" width="300" label="登录名"></el-table-column>
        <el-table-column prop="userName" label="用户名"></el-table-column>
        <el-table-column width="100" label="操作">
          <template slot-scope="scope">
            <el-button  type="success" size="mini" plain @click="authorizationHandle(scope.row.userId)">授权</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--分页-->
      <pagination :total="page.total" :pageSize="page.pageSize" @PageTurning="PageTurning"> </pagination>
      <!-- 弹窗，授权 -->
      <grant-authorization v-if="grantAuthorizationVisible" ref='grantAuthorization'></grant-authorization>
    </el-main>
  </el-container>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue";
  import GrantAuthorization from './grant-authorization.vue'
  import pagination from '@/components/baseComponents/Pagination.vue'
  export default{
    props:['siteContentViewHeight'],
    components:{
      orgTree,GrantAuthorization,pagination
    },
    data(){
      return{
        treeData:[],
        dataList:[],
        dataListLoading:false,
        grantAuthorizationVisible:false,
        page: {
          pageIndex:this.API.constObj.pageIndex,//当前页码
          pageSize:this.API.constObj.pageSize,//每页大小
          total: 0//总条数
        }
      }
    },
    created () {
      this.getDataTree()
    },
    methods:{
      getDataTree(){//获取机构
        this.$http({
          url: this.$http.adornUrl(this.API.getOrganizationTree),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            if(data.data.length>0){
              this.treeData=data.data;
              this.orgTreeClick(this.treeData[0])
            }
          }else{
            this.$message.error(data.msg);
            this.treeData=[];
          }
        });
      },
      //机构树点击事件
      orgTreeClick(data) {
        this.getDataList(data.id);
      },
      getDataList(id){// 获取数据列表
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl(this.API.getSysPoweruser),
          method: 'post',
          data: this.$http.adornData({
            orgId:id,
            current:this.page.pageIndex,
            pageSize:this.page.pageSize
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.dataList=data.data.records;
            this.page.total=data.data.total;
          }else{
            this.$message.error(data.msg);
            this.dataList=[];
            this.page.total=0;
          }
          this.dataListLoading = false
        });
      },
      authorizationHandle(id){//授权弹窗
        this.grantAuthorizationVisible=true;
        this.$nextTick(() => {
          this.$refs.grantAuthorization.init(id)
        })
      },
      //分页组件回调方法
      PageTurning(page_obj) {
      	this.page.pageIndex=page_obj.page_index;
      	this.page.pageSize=page_obj.page_size;
      	this.getDataList();
      }
    }
  }
</script>

<style lang="scss" scoped>
  .permission-assignment{
    .el-main{padding-bottom: 0;}
    .el-aside{padding-top: 10px;border-right: 1px solid #e6e6e6;}
    >.el-main >.el-table--border{border-left: none;}
  }
</style>



